<template>
    <div>
      <div class="time" v-html="time"></div>
      <div class="menu_list">
          <ul class="flex">
            <li><router-link to="/line">Round 1</router-link></li>
            <li  class="active"><router-link to="/line/line_2">Round 2</router-link></li>
            <li><router-link to="/line/line_3">總榜</router-link></li>
