          </div>
      </div>  
      <div class="no_data">
        <div class="lds-ring"><div></div><div></div><div></div><div></div></div>
      </div>
    </div>
</template>
<script>
import $ from "jquery"
export default {
  name: 'page',
  data () {
    return {
      time: '12/07 (六) 19:50:00－12/07 (六) 20:19:59<br>活動已結束',
      raiders:'＃僅計算 Combo 禮物「聖誕糖果槌」，其他禮物計入總榜<br>＃前 20 名主播最高可獲得<span class="span_color">總榜加分 50 萬分</span><br>＃前 10 名主播且粉絲前 5 名，可獲得進場動畫'
    }
  }, 
}
</script>


