import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import Page from '@/components/pages/page'
import race_1 from '@/components/pages/race_1'
import race_2 from '@/components/pages/race_2'
import race_3 from '@/components/pages/race_3'
import race_4 from '@/components/pages/race_4'
import race_directions from '@/components/pages/race_directions'

import line from '@/components/line/page'
import line_1 from '@/components/line/line_1'
import line_2 from '@/components/line/line_2'
import line_3 from '@/components/line/line_3'
import line_4 from '@/components/line/line_4'
import line_directions from '@/components/line/line_directions'

import fans from '@/components/fans/page'
import fans_1 from '@/components/fans/fans_1'
import fans_2 from '@/components/fans/fans_2'
import fans_3 from '@/components/fans/fans_3'
import fans_4 from '@/components/fans/fans_4'

Vue.use(Router)

export default new Router({
  routes: [
    {
      name: 'index',
      path: '/',
      component: index
    },
    {
      name: '階段競賽',
      path: '/',
      component: Page,
      children:[
        {
          name: '階段一',
          path: '/',
          component: race_1
        },
        {
          name: '階段二',
          path: 'race_2',
          component: race_2
        },
        {
          name: '階段三',
          path: 'race_3',
          component: race_3
        },
        {
          name: '階段四',
          path: 'race_4',
          component: race_4
        },
        {
          name: '競賽活動說明',
          path: 'race_directions',
          component: race_directions
        },
      ]
    },
    {
      name: '線下競賽',
      path: '/line',
      component: line,
      children:[
        {
          name: 'Round1',
          path: '',
          component: line_1
        },
        {
          name: 'Round2',
          path: 'line_2',
          component: line_2
        },
        {
          name: '總榜',
          path: 'line_3',
          component: line_3
        },
        {
          name: '總榜大獎圖',
          path: 'line_4',
          component: line_4
        },
        {
          name: '線下競賽活動說明',
          path: 'line_directions',
          component: line_directions
        },
      ]
    },
    {
      name: '粉絲榜',
      path: '/fans',
      component: fans,
      children:[
        {
          name: '階段一',
          path: '',
          component: fans_1,
        },
        {
          name: '階段二',
          path: 'fans_2',
          component: fans_2,
        },
        {
          name: '粉絲總榜',
          path: 'fans_3',
          component: fans_3,
        },
        {
          name: '活動說明',
          path: 'fans_4',
          component: fans_4,
        },
      ]
    },
  ]
})



