<template>
  <div id="app">
    <div class="wrap">
        <div class="video_box">
          <video autoplay loop playsinline>
              <source src="./assets/video/video.mp4" type="video/mp4">
          </video>
        </div>
        <div class="sign_wrap">
           <div class="sign_box">
              <div class="sign sign_1"></div>
              <div class="sign sign_2"></div>
              <div class="sign sign_3"></div>
              <div class="sign sign_4"></div>
              <div class="sign sign_5"></div>
              <div class="sign sign_6"></div>
              <div class="sign sign_7"></div>
          </div>
          <div class="sign_btn" @click="lightbox = !lightbox">簽到</div>
        </div>
    </div>
    <router-view/>
    <div class="lightbox" :class="{'lightbox_none':!lightbox}">
        <div class="lightbox_content">您尚未Login！
          <div class="close" @click="lightbox = !lightbox">x</div>
        </div>
        <div class="lightbox_close" @click="lightbox = !lightbox"></div>
    </div>
  </div>
</template>
<script>
export default {
  name: 'App',   
  data () {
    return {
      lightbox:false,        
    }   
  }, 
}
</script>

<style lang="scss">
    @import "./assets/scss/main";   
</style>



