            </div>
          </div>  
        </div><!-- data_box -->
      <!-- data  -->   
    </div>
</template>
<script>
import $ from "jquery"
export default {
  name: 'page',
  data () {
    return {
      time: '11/08 (五) 18:00:00－12/07 (六) 20:59:59<br>活動已結束',
      raiders:'＃前 30 名，冠軍最大獎<span class="span_color">價值 20 萬元</span><br>＃前 10 名可出席美國線下資格，冠軍最大獎<span class="span_color">價值 50 萬元</span>',
      products:[],
      filterText:"",
      isAll:false,
    }
  },
  methods:{
    getProducts(){      
      const vm = this;
      vm.$http.get('api/mktevent.json').then(response => {    
        vm.products = response.data.data;
      })
    },  
    toggleAll(){
      this.isAll = !this.isAll;
    }
  },
  created(){
    this.getProducts()
  },
  computed:{
    filterArray:function(){
      const vm = this;
        return vm.products.filter(function(item){
            return item.userInfo.displayName.match(vm.filterText)
        });
    }
  } 
}
</script>


