<script>
import $ from "jquery"
export default {
  name: 'HelloWorld',
  data () {
    return {
      time: '12/07 (六) 19:05:00－12/07 (六) 19:29:59<br>活動已結束',
      raiders:'＃前 10 名送<span class="span_color">美國戶外曝光版位</span><br>＃完成個人任務送 3 種開播濾鏡「無辜雪人」＋「俏皮耶誕老人」＋「耶誕紅麋鹿」',
      products:[],
      filterText:"",
      isAll:false,
    }
  },
  methods:{
    getProducts(){      
      const vm = this;
      vm.$http.get('api/mktevent.json').then(response => {    
        vm.products = response.data.data;
      })
    },  
    toggleAll(){
      this.isAll = !this.isAll;
    }
  },
  created(){
    this.getProducts()
  },
  computed:{
    filterArray:function(){
      const vm = this;
        return vm.products.filter(function(item){
            return item.userInfo.displayName.match(vm.filterText)
        });
    }
  }, 
}
</script>


