<script>
import $ from "jquery"
export default {
  name: 'race_2',
  data () {
    return {
      time: '11/16 (六) 24:00:00－11/22 (五) 23:59:59<br>活動已結束',
      raiders:'＃前 20 名，最高可獲得<span class="span_color">聖誕獎金 3 萬元</span><br>＃前 30 名，可獲得聖誕客製化音樂盒',
      products:[],
      filterText:"",
      isAll:false,
    }
  },
  methods:{
    getProducts(){      
      const vm = this;
      vm.$http.get('api/mktevent.json').then(response => {    
        vm.products = response.data.data;
      })
    },  
    toggleAll(){
      this.isAll = !this.isAll;
    }
  },
  created(){
    this.getProducts()
  },
  computed:{
    filterArray:function(){
      const vm = this;
        return vm.products.filter(function(item){
            return item.userInfo.displayName.match(vm.filterText)
        });
    }
  },
  mounted(){
    $(function(){
      $(".lead_btn").click(function(){
        $(".lead_btn").removeClass("active");
        $(this).toggleClass("active");
      })
    })
  }
}
</script>


